'use strict';

const React = require('react');
const siteConfig = require('../config.js');

// eslint-disable-next-line import/no-webpack-loader-syntax, import/no-unresolved
const katexStylesheet = require('!css-loader!../static/css/katex/katex.min.css');

const onRenderBody = ({ setHeadComponents }) => {
  const { useKatex } = siteConfig;

  const headComponents = [
    // Preconnects improve font loading
    React.createElement('link', {
      key: 'preconnect-gfonts-1',
      rel: 'preconnect',
      href: 'https://fonts.googleapis.com'
    }),
    React.createElement('link', {
      key: 'preconnect-gfonts-2',
      rel: 'preconnect',
      href: 'https://fonts.gstatic.com',
      crossOrigin: 'anonymous'
    }),
    // Inter for UI/headings and Source Serif 4 for body text
    React.createElement('link', {
      key: 'fonts-inter-source-serif',
      href: 'https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&family=Source+Serif+4:opsz,wght@8..60,400;8..60,500;8..60,700&display=swap',
      rel: 'stylesheet'
    })
  ];

  if (useKatex) {
    headComponents.push(
      React.createElement('style', {
        key: 'katex-inline-stylesheet',
        dangerouslySetInnerHTML: { __html: katexStylesheet.toString() }
      })
    );
  }

  setHeadComponents(headComponents);
};

module.exports = onRenderBody;
